@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@500;700&display=swap);
html{font-size:16px}body{margin:0;text-align:center;font-family:'Open Sans', sans-serif}#home{display:flex;flex-direction:column;justify-content:center;height:100vh;min-height:55vw;color:white}#home #rain-image,#home #rain-video{position:absolute;object-fit:cover;object-position:50% 0%;width:100vw;height:100vh;min-height:inherit}#home #rain-image{z-index:-2}#home #rain-video{z-index:-1}#home #home-content{-webkit-filter:drop-shadow(0 0 0.1rem black);filter:drop-shadow(0 0 0.1rem black)}#home #home-content #name,#home #home-content #roles{margin:0}#home #home-content #name{font-size:3rem;font-weight:700}#home #home-content #roles{margin-bottom:0.25rem;font-size:0.9rem;font-weight:500}#home #home-content #socials{font-size:3rem}#home #home-content #socials *{margin:0 0.5rem}#home #home-content #wip{font-size:1.3rem}#home #home-content #wip *{margin:0 0.5rem}@media (min-width: 60vh){#home #rain-image,#home #rain-video{object-position:0% 100%}}

